<body><div class="pizza is-hidden-mobile"><a href="https://www.paypal.me/AndyGrabow/3" target="_blank"><img src="pizza.png"><div><span>If you like it</span><br><span>buy me a pizza :)</span></div></a></div><div class="container"><div class="section"><h1 class="title marginless">dotfiles<a href="https://github.com/kakulukia/dotfiles" title="Go to the GitHub-Repo"><img class="github" src="github.png"></a></h1><a class="claim" href="https://ohmyz.sh/" target="_blank">Your terminal never felt <i>this</i> good before.â„¢</a></div><div class="section"><asciinema-player class="is-hidden-mobile" id="player" v-pre src="demo.cast" poster="npt:0:01" speed="1.5" idle-time-limit="1"></asciinema-player><iframe class="is-hidden-tablet" width="560" height="315" src="https://www.youtube.com/embed/nkT3tQFLddU?VQ=HD720" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe><h2 class="subtitle">Intro</h2><p>For the last 6 years I have been building up my shell environment and it's the
first thing that gets installed on a new server or Mac. Especially fasd and the reverse
history search is a huge time saver.</p><p>Some of this stuff I didn't even know was possible before I switched to ZSH or
found app X, so I just wanted to share the whole collection. I did a few improvements myself,
but basically it was all out there. This repo enables everybody to get all the goodies at
once and helps me to feel at home, whenever I open a new shell. ðŸ˜Ž
</p><h2 class="subtitle">Installation</h2><p>Be sure you are logged in as the user for whom you want to install this shell
package and that the user has sudo rights.</p><div class="install is-hidden-mobile"><img src="install_dotfiles.png"><div class="command-line"><p><span class="command">curl</span><span class="text"> "https://raw.githubusercontent.com/kakulukia/dotfiles/main/misc/setup.sh"</span><span> |</span><span class="command"> bash</span></p><span class="icon copy" @click="copyInstallCommand"><i class="mdi mdi-content-copy"></i></span></div></div><img class="is-hidden-tablet" src="install-mobile.png"><p>This will install the following:</p><span class="icon"><i class="mdi mdi-signal-4g mdi-light"></i></span><ul><li>GIT to be able to download the repo itself</li><li>ZSH as your new default shell</li><li>prezto</li><li>Python3 (ghar is a Python script)</li><li>ghar for managing the dotfiles itself (links everything into your home folder)</li><li>SpaceVim</li><li>fasd</li><li>fzf - mainly for completion</li><li>some handy aliases</li><li>starship</li></ul><p><strong>Note:</strong> Use the <span class="command">~/.gitconfig-personal</span> file to set your git username.
That file is imported in <span class="command">.gitconfig</span> so that settings can be ported via this
repo, but the username stays personal.
</p><p>In the misc folder you can find:</p><ul><li>the Powerline patched SourceCode font (you may use any <a href="https://www.nerdfonts.com/" target="_blank">Nerdfont</a>)</li><li>my currently used Dracula.itermcolors</li><li>better-osx-settings script</li><li>an opinionated set of tools (<a href="https://github.com/kakulukia/dotfiles/blob/master/misc/essentials.txt">these</a> and
<a href="https://github.com/kakulukia/dotfiles/blob/master/misc/additional-stuff.sh">those</a>)
</li></ul><p>The dotfiles repo can be updated with the <span class="command">update-dotfiles</span> alias.</p><h2 class="subtitle">Features</h2><p>Apart from what's shown in the recording, here is some more of what's included:</p><ul><li>suggestions (grey text) - use the right arrow key to accept</li><li>history reverse search - use the up arrow to cycle through previous commands.
Anything you typed before hitting up will be used as filter and highlighted.

This seriously saves a lot of typing. ðŸ˜‡<br><br><figure><img src="history.png" title="context sensitive history search"><figcaption>migr + up arrow</figcaption></figure></li><li>red / green colored commands - shows that the command is (un)available</li><li>. is in the path so no need for any ./ prefixing executables</li><li>path<ul><li>print a sorted version of $PATH</li><li>add the given folder to $PATH and</li><li>append the given path to your .profile when called with <span class="command">path --save DIR</span></li></ul></li><li>mv - no need to type the file name twice for renaming</li><li>top - is aliased to glances if installed</li><li>ctop - for docker containers</li><li>,, - jump to the git root dir</li><li>o - will open the finder in the current directory</li><li><a href="https://github.com/kakulukia/dotfiles/tree/master/bin">cd to.app</a> - there is an app in the bin folder that provides a
way to reverse the above trick and open a terminal at the current finder location.
Use the command key to just drag it into the finders toolbar.<br><br><figure><img src="cdto.png" title="cdto in the finder"><figcaption>The result will look like this</figcaption></figure></li><li><a href="https://github.com/kakulukia/dotfiles/tree/master/misc/QuickLook">QuickLook Plugins</a> - there are a few collected plugins for OSX to preview some more files like Markdown</li><li>tm - there's a tmux theme and tm will reconnect to to your last session or create a new one<br>I also changed the default prefix to CTRL+SPACE for easier usage.</li><li>diff - aliased to diff-so-fancy in general, not just the git version</li><li>co - use fasd to launch any known file in VS Code</li><li>errorcode - in general I don't see the point in displaying the error code, hence the sad smiley in the prompt, but if you really wanna know, use that alias (unless you can remember the #?)</li><li>ips - will show all local ips (IPV4) / ip will show some info about your external one</li><li>ping - is aliased to prettyping</li><li>go-reload - hot reload for go apps you are working on</li><li>mgs - multi git status - if your company/current project totally embraces micro services (ðŸ™ˆ) this might come in handy to update em all in one go</li><li>rg - alias for "rg -S --max-columns 444" won't clutter the screen with nasty one line files</li><li>customize startship.toml to create a custom prompt</li><li>up - that's the live preview pipe thing you saw at the end of the screencast. It's activated with CTRL+P for pipe.</li><li>and much more .. but that's about the stuff I frequently use :)</li></ul><h2 class="subtitle">ToDo</h2><ul><li>colorls is a lil slow - there is a promising alternative, but<a href="https://github.com/Peltoche/lsd/issues/92" target="_blank"> it's not looking sexy yet</a></li><li>Decoupling this shell from needing Python. I am a Python developer, but sure it's not really needed for the shell itself. Currently the sync tool ghar is a Python script, tho.</li><li>This<a href="https://www.atlassian.com/git/tutorials/dotfiles" target="_blank"> trick</a> looked kinda nice, i tried it, but it looked and felt messy. ðŸ™„<br>Im probably going to create a shell script to link my files the the HOME directory.</li><li>I tried zinit and zplug, but both failed to just work without extensive care. :/<br>But the current startup speed is not that bad:<figure><img src="benchmark.png" title="startup benchmark"><figcaption>current startup speed with all features enabled</figcaption></figure></li></ul><h2 class="subtitle">Credits</h2><p>This theme was inspired by<a href="https://github.com/Bash-it/bash-it/blob/master/themes/powerline-plain/powerline-plain.theme.bash" target="_blank"> PowerlinePlain</a> and based on<a href="https://github.com/caiogondim/bullet-train-oh-my-zsh-theme" target="_blank"> BulletTrain</a>.<br><br><strong>Generally:</strong> Mad props to all awesome devs who build most of the apps referenced here.
Too many to list em all, but most if not all do feature a credit line inside the scripts.</p></div></div></body>